import React, { useState, useEffect } from 'react';

function App(){

  const [data, setData] = useState({})

  useEffect(() => {
    fetchData();
  },[])

async function fetchData() {
  let response = await fetch('http://127.0.0.1:5000/cv')
  response = await response.json()
  setData(response.items)
}



  return(
    <div>
      
    
      {console.log(data)}
    </div>
  )
}

export default App